 import Vue from 'vue'
import Router from 'vue-router'
import Recommand from '../components/recommand/recommand'
import Rank from '../components/rank/rank'
import Singer from '../components/singer/singer'
import Search from '../components/search/search'
import SingerDetail from '../components/singerDetail/singerDetail'
import Disc from '../components/disc/disc'
import TopList from '../components/toplist/toplist'
import User from '../components/userCenter/userCenter'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
     redirect:'/recommand'
    },
    {
      path: '/recommand',
      name: 'Recommand',
      component: Recommand,
      children:[
        {
          path:':id',
          component:Disc
        }
      ]
    },
    {
      path: '/rank',
      name: 'Rank',
      component: Rank,
      children:[{
        path:':id',
        component:TopList
      }]
    },
    {
      path:'/user',
      component:User,
      name:'User'
    },
    {
      path: '/singer',
      name: 'Singer',
      component: Singer,
      children:[
        {
          path:':id',
          name:'SingerDetail',
          component:SingerDetail
        }
      ]
    },
    {
      path: '/search',
      name: 'Search',
      component: Search,
      children:[{
        path:':id',
        
        component:SingerDetail
      }]
    }
   ]
}) 
 


