<!--  -->
<template>
   <transition name="slide">
    <div class="user-center">
      <div class="back" @click="back">
        <i class="icon-back"></i>
      </div>
      <div class="switches-wrapper">
       <switches :switches="switches" :currentIndex="currentIndex" @switch="switchItem"></switches>
      </div>
      <div ref="playBtn" class="play-btn" @click="random">
        <i class="icon-play"></i>
        <span class="text">随机播放全部</span>
      </div>
      <div class="list-wrapper" ref="listWrapper">
        <scroll ref="favoriteList" class="list-scroll" v-if="currentIndex===0" :data="favoriteSongs">
          <div class="list-inner">
            <song-list :songs="favoriteSongs" @selectSong="selectSong"></song-list>
          </div>
        </scroll>
        <scroll ref="playList" class="list-scroll" v-if="currentIndex===1" :data="playHistory">
          <div class="list-inner">
            <song-list :songs="playHistory" @selectSong="selectSong"></song-list>
          </div>
        </scroll>
      </div>
      <div class="no-result-wrapper" v-show="noResult">
        <no-result :title="noResultDesc"></no-result>
      </div>
    </div>
  </transition>
</template>

<script>
import {mapActions,mapGetters} from 'vuex'
import switches from 'base/switch/switch'
import SongList from 'base/songList/songList'
import scroll from 'base/scroll/scroll'
import noResult from 'base/noResult/noResult'
import {playmini} from 'common/js/mixin'
export default {
    mixins:[playmini],
  data () {
    return {
        currentIndex: 0,
        switches: [
          {
            name: '我喜欢的'
          },
          {
            name: '最近听的'
          }
        ]
    };
  },

  components: {
      SongList,
      switches,
      scroll,
      noResult
  },

  computed: {
      ...mapGetters(['favoriteSongs','playHistory']),
      noResultDesc(){
           if (this.currentIndex === 0) {
          return '暂无收藏歌曲'
        } else {
          return '你还没有听过歌曲'
        }
      },
      noResult(){
            if (this.currentIndex === 0) {
          return !this.favoriteSongs.length
        } else {
          return !this.playHistory.length
        }
      }
  },

  mounted: function(){
      if(this.currentIndex == 0){
            this.$refs.favoriteList.refresh()
        }else{
               this.$refs.playList.refresh()
        }
  },

  methods: {
      handlePlaylist(playList){
        let bottom = playList.length>0?'60px':''
        if(this.currentIndex == 0){
   this.$refs.listWrapper.style.bottom = bottom
        }else{
               this.$refs.playList.style.bottom = bottom
        }
     

      },
      back(){
          this.$router.back()
      },
      switchItem(index){
          this.currentIndex= index
      },
      random(){
        let list = this.currentIndex === 0 ? this.favoriteSongs : this.playHistory
         if(list.length == 0)return
          this.randomPlay(list)
      },
      selectSong(item){
        this.insertSong(item)
      },
      ...mapActions(['randomPlay','insertSong'])
      
  }
}

</script>
<style lang='stylus' scoped>
@import "~common/stylus/variable"

  .user-center
    position: fixed
    top: 0
    bottom: 0
    z-index: 100
    width: 100%
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .switches-wrapper
      margin: 10px 0 30px 0
    .play-btn
      box-sizing: border-box
      width: 135px
      padding: 7px 0
      margin: 0 auto
      text-align: center
      border: 1px solid  $color-text-l
      color: $color-text-l
      border-radius: 100px
      font-size: 0
      .icon-play
        display: inline-block
        vertical-align: middle
        margin-right: 6px
        font-size: $font-size-medium-x
      .text
        display: inline-block
        vertical-align: middle
        font-size: $font-size-small
    .list-wrapper
      position: absolute
      top: 110px
      bottom: 0
      width: 100%
      .list-scroll
        height: 100%
        overflow: hidden
        .list-inner
          padding: 20px 30px
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>


